"use client";
import { createContext, useContext, useEffect, useState, ReactNode } from "react";
import { supabase } from "@/lib/supabase";

export interface Design {
  id: string;
  image: string;
  roomType: string;
  style: string[];
}

export interface FilterState {
  styles: string[];
  roomTypes: string[];
  colors: string[];
  materials: string[];
  propertyType: string[];
  budget: string | null;
}

interface AppContextType {
  user: { id: string; email?: string | null } | null;

  filters: FilterState;
  setFilters: (patch: Partial<FilterState>) => void;
  resetFilters: () => void;

  favorites: Design[];
  addFavorite: (d: Design) => Promise<void>;
  removeFavorite: (id: string) => Promise<void>;
  clearFavorites: () => Promise<void>;
}

const DEFAULT_FILTERS: FilterState = {
  styles: [],
  roomTypes: [],
  colors: [],
  materials: [],
  propertyType: [],
  budget: null,
};

const LOCAL_KEY = "ds_favorites";
const AppContext = createContext<AppContextType | undefined>(undefined);

export function AppProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<AppContextType["user"]>(null);
  const [filters, setFiltersState] = useState<FilterState>(DEFAULT_FILTERS);
  const [favorites, setFavorites] = useState<Design[]>([]);

  // --- AUTH: гидрация сессии и подписка на изменения
  useEffect(() => {
    (async () => {
      const { data: { session } } = await supabase.auth.getSession();
      if (session?.user) {
        setUser({ id: session.user.id, email: session.user.email });
      }
    })();
    const { data: sub } = supabase.auth.onAuthStateChange((_evt, sess) => {
      if (sess?.user) setUser({ id: sess.user.id, email: sess.user.email });
      else setUser(null);
    });
    return () => { sub.subscription.unsubscribe(); };
  }, []);

  // --- FAVORITES: загрузка
  useEffect(() => {
    (async () => {
      if (user) {
        // загружаем с сервера
        const { data, error } = await supabase
          .from("favorites")
          .select("design_id, image, room_type, styles")
          .order("created_at", { ascending: false });
        if (!error && data) {
          setFavorites(
            data.map((r) => ({
              id: r.design_id,
              image: r.image,
              roomType: r.room_type ?? "Unknown",
              style: Array.isArray(r.styles) ? r.styles : [],
            }))
          );
          // объединяем локальные лайки с сервером (one-time merge)
          try {
            const localRaw = localStorage.getItem(LOCAL_KEY);
            if (localRaw) {
              const local: Design[] = JSON.parse(localRaw);
              const toUpload = local.filter(L => !data.find(D => D.design_id === L.id));
              if (toUpload.length) {
                await supabase.from("favorites").insert(
                  toUpload.map((d) => ({
                    design_id: d.id,
                    image: d.image,
                    room_type: d.roomType,
                    styles: d.style,
                  }))
                );
                localStorage.removeItem(LOCAL_KEY);
              }
            }
          } catch {}
        }
      } else {
        // гость → localStorage
        try {
          const raw = localStorage.getItem(LOCAL_KEY);
          setFavorites(raw ? JSON.parse(raw) : []);
        } catch { setFavorites([]); }
      }
    })();
  }, [user]);

  // --- HELPERS
  const setFilters = (patch: Partial<FilterState>) =>
    setFiltersState((prev) => ({ ...prev, ...patch }));
  const resetFilters = () => setFiltersState(DEFAULT_FILTERS);

  const persistLocal = (next: Design[]) => {
    try { localStorage.setItem(LOCAL_KEY, JSON.stringify(next)); } catch {}
  };

  const addFavorite = async (d: Design) => {
    setFavorites((prev) => {
      if (prev.some((x) => x.id === d.id)) return prev;
      const next = [d, ...prev];
      if (!user) persistLocal(next);
      return next;
    });
    if (user) {
      await supabase.from("favorites").insert({
        design_id: d.id, image: d.image, room_type: d.roomType, styles: d.style,
      });
    }
  };

  const removeFavorite = async (id: string) => {
    setFavorites((prev) => {
      const next = prev.filter((x) => x.id !== id);
      if (!user) persistLocal(next);
      return next;
    });
    if (user) {
      await supabase.from("favorites").delete().eq("design_id", id);
    }
  };

  const clearFavorites = async () => {
    setFavorites([]);
    if (!user) persistLocal([]);
    if (user) {
      await supabase.from("favorites").delete().neq("design_id", "___never___");
    }
  };

  return (
    <AppContext.Provider value={{
      user,
      filters, setFilters, resetFilters,
      favorites, addFavorite, removeFavorite, clearFavorites
    }}>
      {children}
    </AppContext.Provider>
  );
}

export const useApp = () => {
  const ctx = useContext(AppContext);
  if (!ctx) throw new Error("useApp must be used inside AppProvider");
  return ctx;
};
